
import React from 'react';
import axios from 'axios';


export class SongInfo extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            song: {}
        }
    }

    componentDidMount() {
        const id= this.props.match.params.id;
        axios.get('/song?id='+id)
        .then((res) => {
            this.setState({song:res.data})
        });

    }

    render() {
        return (
            <div className="container info-card">
            <div><span className="info-text">Name</span>:  <span className="info-value">{this.state.song.name}</span></div>
            <div><span className="info-text">Artist</span>:<span className="info-value">{this.state.song.artists}</span></div>
            <div><span className="info-text">Rank</span>:  <span className="info-value">{this.state.song.rank}</span></div>
            <div><span className="info-text">Danceability</span>:  <span className="info-value">{this.state.song.danceability}</span></div>
            <div><span className="info-text">Energy</span>:  <span className="info-value">{this.state.song.energy}</span></div>
            <div><span className="info-text">Loudness</span>:  <span className="info-value">{this.state.song.loudness}</span></div>
            <div><span className="info-text">Speechiness</span>:  <span className="info-value">{this.state.song.speechiness}</span></div>
            <div><span className="info-text">Tempo</span>:  <span className="info-value">{this.state.song.tempo}</span></div>
            
            </div>
        )
    }
}
