import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { Song } from './component/song/song';
import { BrowserRouter, Route, Redirect } from 'react-router-dom';
import {SongInfo} from './component/song/songinfo';

class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <div className="app">
          <header className="app-header">
            Spotify
        </header>
          <Route exact path='' render={() => <Redirect to='/songs' />} />
          <Route exact path='/songs' component={Song} />
          <Route exact path='/songInfo/:id' component={SongInfo} />
          </div>
      </BrowserRouter>
        );
      }
    }
    
    export default App;
