import React from 'react';
import { SongList } from './songlist';
import axios from 'axios';
import REGIONAL_GLOBAL_DEFAULT from '../../REGIONAL_GLOBAL_DEFAULT.jpg';

export class Song extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            songs: [],
            search: ''
        }
    }

    componentDidMount() {
        let stocks = [];
        axios.get('/topsongs')
            .then(res => {
                this.setState({ songs: res.data })
            });
    }

    searchChange(e) {
        const name = e.target.value;
        if (name !== "") {
            axios.get('/songs?name=' + name).then(res => {
                this.setState({ songs: res.data })
            });
        } else {
            axios.get('/topsongs')
                .then(res => {
                    this.setState({ songs: res.data })
                });
        }
    }

    handleSort(key,sort) {
        // function for dynamic sorting
    function compareValues(key, order='asc') {
    return function(a, b) {
      if(!a.hasOwnProperty(key) || !b.hasOwnProperty(key)) {
        // property doesn't exist on either object
        return 0;
      }
  
      const varA = (typeof a[key] === 'string') ?
        a[key].toUpperCase() : a[key];
      const varB = (typeof b[key] === 'string') ?
        b[key].toUpperCase() : b[key];
  
      let comparison = 0;
      if (varA < varB) {
        comparison = 1;
      } else if (varA > varB) {
        comparison = -1;
      }
      return (
        (order === 'desc') ? (comparison * -1) : comparison
      );
    };
  } 
    const songs = this.state.songs;
    songs.sort(compareValues(key,sort));
    this.setState({songs:songs});
    }

    filter(e) {
        let value=e.split("-")
    this.handleSort(value[1],value[0]);
    }

    render() {
        return (
            <div className="container song-container">
                <div className="row">
                    <div className="col-sm-4"></div>
                    <div className="col-sm-4">
                        <input type="text"
                            onChange={(e) => { this.searchChange(e) }}
                            placeholder="Search by name or artist ..."
                            className="form-control" id="usr"></input>
                    </div>
                    <div className="col-sm-4">
                        <div className="dropdown">
                            <button type="button" className="btn btn-primary dropdown-toggle" data-toggle="dropdown">
                                Sort By
                            </button>
                            <div className="dropdown-menu">
                                <h5 className="dropdown-header">Ascending</h5>
                                <div className="dropdown-item" id="asc-duration_ms" onClick={(e) => { this.filter(e.target.id) }}>Time</div>
                                <div className="dropdown-item" id="asc-danceability" onClick={(e) => { this.filter(e.target.id) }}>Danceability</div>
                                <div className="dropdown-item" id="asc-loudness" onClick={(e) => { this.filter(e.target.id) }}>Loudness</div>
                                <div className="dropdown-item" id="asc-speechiness" onClick={(e) => { this.filter(e.target.id) }}>speechiness</div>
                                <h5 className="dropdown-header">Descending</h5>
                                <div id="desc-duration_ms" className="dropdown-item" onClick={(e) => { this.filter(e.target.id) }}>Time</div>
                                <div id="desc-danceability" className="dropdown-item" onClick={(e) => { this.filter(e.target.id) }}>Danceability</div>
                                <div id="desc-loudness" className="dropdown-item" onClick={(e) => { this.filter(e.target.id) }}>Loudness</div>
                                <div id="desc-speechiness" className="dropdown-item" onClick={(e) => { this.filter(e.target.id) }} >speechiness</div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="row">
                    <div className="col-sm-4">
                        <img className="image" src={REGIONAL_GLOBAL_DEFAULT}></img>
                    </div>
                    <div className="col-sm-8">
                        <SongList songs={this.state.songs} />
                    </div>
                </div>
            </div>
        )
    }
}