import React from 'react';
import { Link } from 'react-router-dom'
const SongCard = (props) => {
    
    const song = props.song;

    const millisToMinutesAndSeconds = (millis) => {
        var minutes = Math.floor(millis / 60000);
        var seconds = ((millis % 60000) / 1000).toFixed(0);
        return minutes + ":" + (seconds < 10 ? '0' : '') + seconds;
    }

    return (
        <Link style={{ textDecoration: 'none' }} to={"/songInfo/" + song.name}>
        <div className="song-card">
            <div className="col-xs-12 col-lg-9 col-xl-8">

                <div className="row">
                    <div className="col-sm-1"><i className="fa fa-music"></i></div>
                    <div className="col-lg-9">{song.name}</div>
                    <div className="col-sm-2">{millisToMinutesAndSeconds(song.duration_ms)}</div>
                </div>

                <div className="row">
                    <div className="col-sm-1"></div>
                    <div className="col-4">{song.artists}</div></div>
            </div>
        </div>
        </Link>
    )
}
export default SongCard;